import React, { useState } from "react";
import Slider from "react-slick";
import Observer from "@researchgate/react-intersection-observer";
import { BsArrowUp } from "react-icons/bs";
import useWindowSize from "../WindowSize";
import "../../App.css";
import "./Works.css";

import pat03 from "../../assets/pattern03.png";
import pat01 from "../../assets/pattern01.png";
import pat02 from "../../assets/pattern02.png";
import pat04 from "../../assets/pattern04.png";

import art01 from "../../assets/artwork01.png";
import art02 from "../../assets/artwork02.png";
import art03 from "../../assets/artwork03.png";
import art04 from "../../assets/artwork04.png";

import log01 from "../../assets/logo01.png";
import log02 from "../../assets/logo02.png";
import log03 from "../../assets/logo03.png";
import log04 from "../../assets/logo04.png";

import prod01 from "../../assets/product01.png";
import prod02 from "../../assets/product02.png";
import prod03 from "../../assets/product03.png";
import prod04 from "../../assets/product04.png";

import hoopwines from "../../assets/hoopwines.png";
import rustica from "../../assets/rustica.png";
import cutterart from "../../assets/cutterart.png";
import rozsabimbo from "../../assets/rozsabimbo.png";
import nectaris from "../../assets/nectaris.png";
import bidmaster from "../../assets/bidmaster.png";
import wipeit from "../../assets/wipeit.png";
import amacord from "../../assets/amacord.png";
import borsodfestek from "../../assets/borsodfestek.png";

import electricity from "../../assets/electricity.jpg";
import greentea from "../../assets/greentea.jpg";
import meseloajandek from "../../assets/meseloajandek.jpg";
import soap from "../../assets/soap.jpg";

import patt01 from "../../assets/pat01.jpg";
import patt02 from "../../assets/pat02.jpg";
import patt03 from "../../assets/pat03.jpg";
import patt04 from "../../assets/pat04.jpg";
import patt05 from "../../assets/pat05.jpg";
import patt06 from "../../assets/pat06.jpg";
import patt07 from "../../assets/pat07.jpg";
import patt08 from "../../assets/pat08.jpg";
import patt09 from "../../assets/pat09.jpg";
import patt10 from "../../assets/pat10.jpg";

import artwork from "../../assets/artwork.jpg";

import { Logos } from "../Logos/Logos";
import { Patterns } from "../Patterns/Patterns";

export default function Works() {
  const [currentImage, setCurrentImage] = useState(Logos[0].image);
  const [currentId, setCurrentId] = useState(1);
  const [currentId2, setCurrentId2] = useState(1);
  const [currentPat, setCurrentPat] = useState(Patterns[0].image);
  const [showScroll, setScroll] = useState("none");
  const [visibleElement, setElement] = useState("");
  const slides = useWindowSize();

  async function test(id) {
    setElement(id);
  }

  const handleClick = (id) => {
    Logos.map((item) => {
      if (item.id == id) {
        setCurrentImage(item.image);
      }
    });
    setCurrentId(id);
  };
  const handleClick2 = (id) => {
    Patterns.map((item) => {
      if (item.id == id) {
        setCurrentPat(item.image);
      }
    });
    setCurrentId2(id);
  };

  var settings = {
    infinite: true,
    speed: 500,
    slidesToShow: slides,
    slidesToScroll: 1,
  };

  var settings2 = {
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
  };

  const checkScrollTop = () => {
    if (showScroll === "none" && window.pageYOffset > 600) {
      setScroll("flex");
    } else if (showScroll === "flex" && window.pageYOffset <= 600) {
      setScroll("none");
    }
  };

  window.addEventListener("scroll", checkScrollTop);

  const scrollTop = () => {
    window.scrollTo({ top: 0, behavior: "smooth" });
  };

  return (
    <div className="container3">
      <div className="sidebar" style={{ display: showScroll }}>
        <a
          href="#logos"
          className={
            visibleElement == "logo" ? "activeElement" : "normalElement"
          }
        >
          Logos
        </a>
        <a
          href="#productdesign"
          className={
            visibleElement == "product" ? "activeElement" : "normalElement"
          }
        >
          Product Design
        </a>
        <a
          href="#patterns"
          className={
            visibleElement == "pattern" ? "activeElement" : "normalElement"
          }
        >
          Patterns
        </a>
        <a
          href="#artwork"
          className={
            visibleElement == "artworks" ? "activeElement" : "normalElement"
          }
        >
          Artworks
        </a>
      </div>
      <a
        className="upiconbg"
        style={{ display: showScroll }}
        onClick={scrollTop}
      >
        <div className="upicon">
          <BsArrowUp />
        </div>
        <div
          style={{ padding: "3px", paddingRight: "10px", fontWeight: "bold" }}
        >
          Scroll up
        </div>
      </a>

      <div className="image-grid-row">
        <div className="bubble01"></div>
        <div className="imgcont01">
          <div id="images01">
            <img src={pat01} />
            <img src={pat02} />
            <img src={pat03} />
            <img src={pat04} />
          </div>
          <div className="see-more01">
            <p>See more</p>
          </div>
          <a href="#patterns">
            <div className="text-on-img01">
              <p>Patterns</p>
            </div>
          </a>
        </div>
        <div className="bubble02"></div>
        <div className="imgcont02">
          <div id="images02">
            <img src={art01} />
            <img src={art02} />
            <img src={art03} />
            <img src={art04} />
          </div>
          <div className="see-more02">
            <p>See more</p>
          </div>
          <a href="#artwork">
            <div className="text-on-img02">
              <p>Artwork</p>
            </div>
          </a>
        </div>
        <div className="bubble03"></div>
        <div className="bubble04"></div>
      </div>
      <div className="image-grid-row2">
        <div className="bubble05"></div>
        <div className="bubble06"></div>
        <div className="imgcont03">
          <div id="images03">
            <img src={log01} />
            <img src={log02} />
            <img src={log03} />
            <img src={log04} />
          </div>
          <div className="see-more03">
            <p>See more</p>
          </div>
          <a href="#logos">
            <div className="text-on-img03">
              <p>Logos</p>
            </div>
          </a>
        </div>
        <div className="bubble07"></div>
        <div className="imgcont04">
          <div id="images04">
            <img src={prod01} />
            <img src={prod02} />
            <img src={prod03} />
            <img src={prod04} />
          </div>
          <div className="see-more04">
            <p>See more</p>
          </div>
          <a href="#productdesign">
            <div className="text-on-img04">
              <p>Product design</p>
            </div>
          </a>
        </div>
        <div className="bubble08"></div>
      </div>
      <div className="c-full">
        <Observer onChange={() => test("logo")} threshold={[0.6]}>
          <div id="logos">
            <Slider {...settings}>
              <div>
                <btn onClick={() => handleClick("1")}>
                  <img
                    src={hoopwines}
                    width="100%"
                    height="auto"
                    className={currentId == 1 ? "logobtn-active" : "logobtn"}
                  />
                </btn>
              </div>
              <div>
                <btn onClick={() => handleClick("2")}>
                  <img
                    src={rustica}
                    width="100%"
                    height="auto"
                    className={currentId == 2 ? "logobtn-active" : "logobtn"}
                  />
                </btn>
              </div>
              <div>
                <btn onClick={() => handleClick("3")}>
                  <img
                    src={cutterart}
                    width="100%"
                    height="auto"
                    className={currentId == 3 ? "logobtn-active" : "logobtn"}
                  />
                </btn>
              </div>
              <div>
                <btn onClick={() => handleClick("4")}>
                  <img
                    src={rozsabimbo}
                    width="100%"
                    height="auto"
                    className={currentId == 4 ? "logobtn-active" : "logobtn"}
                  />
                </btn>
              </div>
              <div>
                <btn onClick={() => handleClick("5")}>
                  <img
                    src={nectaris}
                    width="100%"
                    height="auto"
                    className={currentId == 5 ? "logobtn-active" : "logobtn"}
                  />
                </btn>
              </div>
              <div>
                <btn onClick={() => handleClick("6")}>
                  <img
                    src={bidmaster}
                    width="100%"
                    height="auto"
                    className={currentId == 6 ? "logobtn-active" : "logobtn"}
                  />
                </btn>
              </div>
              <div>
                <btn onClick={() => handleClick("7")}>
                  <img
                    src={wipeit}
                    width="100%"
                    height="auto"
                    className={currentId == 7 ? "logobtn-active" : "logobtn"}
                  />
                </btn>
              </div>
              <div>
                <btn onClick={() => handleClick("8")}>
                  <img
                    src={amacord}
                    width="100%"
                    height="auto"
                    className={currentId == 8 ? "logobtn-active" : "logobtn"}
                  />
                </btn>
              </div>
              <div>
                <btn onClick={() => handleClick("9")}>
                  <img
                    src={borsodfestek}
                    width="100%"
                    height="auto"
                    className={currentId == 9 ? "logobtn-active" : "logobtn"}
                  />
                </btn>
              </div>
            </Slider>
            <div style={{ marginTop: "30px" }}>{currentImage}</div>
          </div>
        </Observer>
        <Observer onChange={() => test("product")} threshold={[0.6]}>
          <div id="productdesign">
            <Slider {...settings2}>
              <div>
                <img
                  className="unit-image"
                  src={electricity}
                  width="80%"
                  height="auto"
                />
              </div>
              <div>
                <img
                  className="unit-image"
                  src={meseloajandek}
                  width="100%"
                  height="auto"
                />
              </div>
              <div>
                <img
                  className="unit-image"
                  src={greentea}
                  width="100%"
                  height="auto"
                />
              </div>
              <div>
                <img
                  className="unit-image"
                  src={soap}
                  width="100%"
                  height="auto"
                />
              </div>
            </Slider>
          </div>
        </Observer>
        <Observer onChange={() => test("pattern")} threshold={[0.6]}>
          <div id="patterns">
            <Slider {...settings}>
              <div>
                <btn onClick={() => handleClick2("1")}>
                  <img
                    src={patt01}
                    width="100%"
                    height="auto"
                    className={currentId2 == 1 ? "pat-active" : "patbtn"}
                  />
                </btn>
              </div>
              <div>
                <btn onClick={() => handleClick2("2")}>
                  <img
                    src={patt02}
                    width="100%"
                    height="auto"
                    className={currentId2 == 2 ? "pat-active" : "patbtn"}
                  />
                </btn>
              </div>
              <div>
                <btn onClick={() => handleClick2("3")}>
                  <img
                    src={patt03}
                    width="100%"
                    height="auto"
                    className={currentId2 == 3 ? "pat-active" : "patbtn"}
                  />
                </btn>
              </div>
              <div>
                <btn onClick={() => handleClick2("4")}>
                  <img
                    src={patt04}
                    width="100%"
                    height="auto"
                    className={currentId2 == 4 ? "pat-active" : "patbtn"}
                  />
                </btn>
              </div>
              <div>
                <btn onClick={() => handleClick2("5")}>
                  <img
                    src={patt05}
                    width="100%"
                    height="auto"
                    className={currentId2 == 5 ? "pat-active" : "patbtn"}
                  />
                </btn>
              </div>
              <div>
                <btn onClick={() => handleClick2("6")}>
                  <img
                    src={patt06}
                    width="100%"
                    height="auto"
                    className={currentId2 == 6 ? "pat-active" : "patbtn"}
                  />
                </btn>
              </div>
              <div>
                <btn onClick={() => handleClick2("7")}>
                  <img
                    src={patt07}
                    width="100%"
                    height="auto"
                    className={currentId2 == 7 ? "pat-active" : "patbtn"}
                  />
                </btn>
              </div>
              <div>
                <btn onClick={() => handleClick2("8")}>
                  <img
                    src={patt08}
                    width="100%"
                    height="auto"
                    className={currentId2 == 8 ? "pat-active" : "patbtn"}
                  />
                </btn>
              </div>
              <div>
                <btn onClick={() => handleClick2("9")}>
                  <img
                    src={patt09}
                    width="100%"
                    height="auto"
                    className={currentId2 == 9 ? "pat-active" : "patbtn"}
                  />
                </btn>
              </div>
              <div>
                <btn onClick={() => handleClick2("10")}>
                  <img
                    src={patt10}
                    width="100%"
                    height="auto"
                    className={currentId2 == 10 ? "pat-active" : "patbtn"}
                  />
                </btn>
              </div>
            </Slider>
            <div style={{ marginTop: "30px" }}>{currentPat}</div>
          </div>
        </Observer>
        <Observer onChange={() => test("artworks")} threshold={[0.6]}>
          <div id="artwork">
            <img
              src={artwork}
              width="100%"
              height="auto"
              style={{ borderRadius: "10px" }}
            />
          </div>
        </Observer>
      </div>
    </div>
  );
}
